html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  background-color: #ffffff;
}

.nav-button {
  background-color: transparent;
  border: none;
}

.btn-carousel {
  width: 48px;
  height: 96px;
  position: absolute;
  top: 55%;
  background-color: #ffffff;
  color: #5ccfd3;
  font-size: 12px;
  font-weight: 700;
  border: none;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}
.btn-carousel.left {
  left: 0;
  border-radius: 0 0.5rem 0.5rem 0;
}
.btn-carousel.right {
  right: 0;
  border-radius: 0.5rem 0 0 0.5rem;
}

nav {
  width: 100%;
  height: 125px;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
nav .nav-right .nav-buttons-wrapper {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: center;
  align-self: center;
}

.carousel {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: row;
  overflow-y: scroll;
  position: relative;
}
.carousel .carousel-item {
  width: 312px;
  height: 450px;
  margin-left: 1.5rem;
  padding-bottom: 2rem;
}
.carousel .carousel-item:last-of-type {
  margin-right: 1.5rem;
}
@media (min-width: 40em) {
  .carousel .carousel-item {
    height: 510px;
  }
}
.carousel .carousel-item .product-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}
.carousel .carousel-item .product-wrapper .product-item-image {
  display: block;
  overflow: hidden;
}
.carousel .carousel-item .product-wrapper .product-item-image img {
  height: 100%;
}
.carousel .carousel-item .product-wrapper .product-item-info {
  width: 100%;
  height: 88px;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5);
}
.carousel .carousel-item .product-wrapper .product-item-info p {
  font-size: 24px;
  margin-left: 1.5rem;
  color: #ffffff;
}

.product-section {
  background: linear-gradient(to bottom, transparent, transparent 78%, #5ccfd3 78%, #5ccfd3 100%);
}
.product-section .container .title-wrapper p {
  margin: 0 2.5rem;
  font-size: 14px;
  font-weight: 500;
}
@media (min-width: 40em) {
  .product-section .container .title-wrapper p {
    font-size: 16px;
  }
}
.product-section .container .title-wrapper h1 {
  margin: 0 2.5rem;
  font-weight: 700;
  font-size: 2rem;
  color: #5ccfd3;
}
@media (min-width: 40em) {
  .product-section .container .title-wrapper h1 {
    font-size: 40px;
  }
}